import { Box, Typography, Container, useTheme } from '@mui/material';
import { motion } from 'framer-motion';
import SchoolIcon from '@mui/icons-material/School';
import { useInView } from 'react-intersection-observer';

const education = [
  {
    school: 'Institute of Space Technology',
    degree: 'Bachelor of Science in Avionics Engineering',
    year: '2018 - 2022',
    thesis: 'Thesis: Detection of Heart Beat using a Micromotion Doppler Radar',
    description: 'Relevant coursework: Digital Signal Processing, Communication Systems, Control Systems, Microprocessors, Electromagnetic Theory, Radar Systems, Embedded Systems, Circuit Design, Electronics'
  }
];

const Education = () => {
  const theme = useTheme();
  const [ref, inView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.5,
      },
    },
  };

  return (
    <Box
      id="education"
      sx={{
        py: { xs: 8, md: 12 },
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        position: 'relative',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'radial-gradient(circle at 50% 30%, rgba(0, 216, 255, 0.03) 0%, rgba(10, 25, 47, 0) 70%)',
          zIndex: 0,
        },
      }}
    >
      <Container maxWidth="lg" sx={{ position: 'relative', zIndex: 1 }}>
        <motion.div
          ref={ref}
          variants={containerVariants}
          initial="hidden"
          animate={inView ? "visible" : "hidden"}
        >
          <Box sx={{ mb: 8, textAlign: 'center' }}>
            <Typography
              variant="body1"
              sx={{
                color: theme.palette.primary.main,
                fontFamily: '"Fira Code", monospace',
                mb: 2,
              }}
            >
              <span style={{ color: theme.palette.primary.main }}>02.</span> Where I've Studied
            </Typography>
            <Typography variant="h2">Education</Typography>
          </Box>

          <Box sx={{ maxWidth: '800px', mx: 'auto' }}>
            {education.map((edu, index) => (
              <motion.div
                key={index}
                variants={itemVariants}
                style={{
                  marginBottom: '1.5rem',
                  position: 'relative',
                }}
              >
                <Box
                  sx={{
                    position: 'relative',
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      left: 0,
                      top: 0,
                      bottom: 0,
                      width: '2px',
                      background: 'linear-gradient(to bottom, #00D8FF, transparent)',
                    },
                  }}
                >
                  <Box
                    sx={{
                      ml: 4,
                      p: 3,
                      background: 'rgba(17, 34, 64, 0.5)',
                      backdropFilter: 'blur(10px)',
                      borderRadius: 2,
                      border: '1px solid rgba(0, 216, 255, 0.1)',
                      transition: 'all 0.3s ease',
                      '&:hover': {
                        transform: 'translateY(-5px)',
                        boxShadow: '0 10px 20px rgba(0, 216, 255, 0.1)',
                        borderColor: '#00D8FF',
                      },
                    }}
                  >
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      <SchoolIcon sx={{ color: '#00D8FF', mr: 2 }} />
                      <Typography
                        variant="h5"
                        component="h3"
                        sx={{
                          color: '#00D8FF',
                          fontWeight: 'bold',
                        }}
                      >
                        {edu.school}
                      </Typography>
                    </Box>
                    <Typography
                      variant="subtitle1"
                      sx={{
                        color: '#E6F1FF',
                        fontWeight: 'medium',
                        mb: 2,
                      }}
                    >
                      {edu.degree}
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{
                        color: 'rgba(230, 241, 255, 0.7)',
                        fontFamily: '"Fira Code", monospace',
                        mb: 2,
                      }}
                    >
                      {edu.year}
                    </Typography>
                    {edu.thesis && (
                      <Typography
                        variant="body2"
                        sx={{
                          color: theme.palette.primary.main,
                          fontStyle: 'italic',
                          mb: 2,
                          borderLeft: `2px solid ${theme.palette.primary.main}`,
                          pl: 2,
                        }}
                      >
                        {edu.thesis}
                      </Typography>
                    )}
                    <Typography
                      variant="body2"
                      sx={{
                        color: 'rgba(230, 241, 255, 0.8)',
                      }}
                    >
                      {edu.description}
                    </Typography>
                  </Box>
                </Box>
              </motion.div>
            ))}
          </Box>
        </motion.div>
      </Container>
    </Box>
  );
};

export default Education; 